:root {
  --pagePaddingY: calc(2vw + 0.5rem);
  --pagePaddingX: calc(2vw + 1rem);

  --dpl-fs-2: 1.75rem;
  --dpl-fs-1: 1rem;
  --dpl-fs-0: 0.75rem;

  --c-accentH: 332;
  --c-accentHS: var(--c-accentH), 100%;
  --c-accentHSL: var(--c-accentHS), 45%;
}

.theme-light {
  --body: hsl(var(--c-accentHS), 15%);
  --background: white;
  --backdrop: hsla(var(--c-accentH), 5%, 10%, 0.5);
  --overlay: hsla(var(--c-accentH), 20%, 90%, 1);
}

.theme-dark {
  --body: hsl(var(--c-accentHS), 85%);
  --background: hsl(var(--c-accentH), 30%, 4%);;
  --backdrop: hsla(var(--c-accentH), 5%, 10%, 0.8);
  --overlay: hsla(var(--c-accentH), 20%, 20%, 1);
}

*, :after, :before {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

::-moz-selection {
  background: hsl(var(--c-accentHS), 90%);
  color: hsl(var(--c-accentHSL));
}
::selection {
  background: hsl(var(--c-accentHS), 90%);
  color: hsl(var(--c-accentHSL));
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: var(--dpl-fs-0);
  line-height: 1.5;
  margin: 0;
  background: var(--background);
  
  color: var(--body);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "tnum";
}

a:focus {
  background: hsl(var(--c-accentHS), 90%);
  color: hsl(var(--c-accentHSL));
  outline: none;
}

code {
  font-size: 0.6rem;
  font-family: "Lucida Console", Monaco, monospace;
}

.dimmed {
  opacity: 0.5;
}